	module.exports = {"data":{"markdownRemark":{"html":"<p>慣れてください。</p>\n<p>……だとこれで終わってしまうのでなんでこんな顔にしたかちょっと語ることにします。\nというかこれが書きたいがためにこのコーナーを作ったわけですので。</p>\n<p>まずは<a href=\"/!/leila/tha_leila.txt\">時封城キャラ設定のレイラの項</a>を挙げておきますので見ていない方はこちらを一読の上先にお進みください。</p>\n<p>ジャケ絵のレイラは時期的には騒霊姉妹を作り出す直前のもので、まだ生きている状態を保っている時のレイラです。\n……が物理的に生きてはいるものの幸せな日々からの落差、長い間屋敷に残り続けた孤独、そして極度の飢餓にさらされもう精神的には人間ではなくなっている状態です。\n人間部屋に閉じ込め一切の交流を禁ずると普段一人のほうが落ち着くとか言ってる人でも数週間で落ち着きをなくし、数ヶ月もすれば狂人と化すとか言われていますし。</p>\n<p>そんな極限と絶望のさなか、ふとしたことで屋敷に封じられていた禁じられたマジックアイテムに手を出してしまったレイラは藁にもすがる思いで姉たちの虚像を作り出す……そしてマジックアイテムからあふれ出た靄は渦を巻きやがて3つに収束し今まさに具現化しようとしている……「やっと会えるんだ」と言う気持ちが溢れ思わず口元がほころぶ……と、丁度そんな光景をイメージして描いて見ました。</p>\n<p>ちなみに時封城本編に出てくるレイラはすでに死後で騒霊姉妹を作り出してから大分経っており、欲求は満たされているのでやつれてないのです。</p>","frontmatter":{"cover":null,"date":"2005-12-09T00:00:02+09:00","strDate":"2005/12/8","title":"レイラの顔が怖いんですが"}}},"pathContext":{"path":"/ja/blog/2005/12/09-leila-faq-1/","langKey":"ja"}}

